import m from 'mithril'
import AppState from './state'

class Home {
  oninit() {}
  oncreate() {
    AppState.loadMCQ(23)
  }
  view() {
    return [
      m(
        'nav.navbar.navbar-dark.bg-dark.shadow.fixed-top',
        m('div.container', [m('a.navbar-brand', 'Gulf MCQ Pro')]),
      ),
      m('div.container', [
        AppState.error
          ? m(Message, {
              color: 'bg-danger',
              message: 'Something went wrong (' + AppState.error + ')',
            })
          : null,
        AppState.busy ? m(Loader) : null,

        AppState.mcq != null && !AppState.busy
          ? m(Question, { data: AppState.mcq })
          : null,
      ]),
      m(
        'div.fixed-bottom.py-2.bg-light.shadow',
        m(
          'div.container',
          m('div.row.d-flex.align-items-center', [
            AppState.index > 1
              ? m(
                  'div.col',
                  m(
                    'a.btn.btn-text',
                    {
                      onclick: (e) => {
                        if (AppState.index > 1) {
                          AppState.index -= 1
                          AppState.loadMCQ()
                        }
                      },
                    },
                    'Prev',
                  ),
                )
              : null,
            m(
              'div.col.text-muted.text-center.fw-bold',
              'Q - ' + AppState.mcq['_id'],
            ),
            AppState.index < AppState.count
              ? m(
                  'div.col.text-end',
                  m(
                    'a.btn.btn-text',
                    {
                      onclick: (e) => {
                        if (AppState.index < AppState.count) {
                          AppState.index += 1
                          AppState.loadMCQ()
                        }
                      },
                    },
                    'Next',
                  ),
                )
              : null,
          ]),
        ),
      ),
    ]
  }
}

class Question {
  oninit() {
    this.state = {
      expanded: false,
    }
  }
  view(vnode) {
    let mcq = vnode.attrs.data
    return [
      m(
        'div.row.mb-3',
        m(
          'div.col.fs-5.ff-serif',
          {
            style: {
              'white-space': 'pre-wrap',
            },
          },
          mcq['stem'],
        ),
      ),
      m(
        'div.row.mb-4',
        m(
          'div.col',
          m('div.list-group', [
            m('div.list-group-item.fs-5.ff-serif', mcq['opt1']),
            m('div.list-group-item.fs-5.ff-serif', mcq['opt2']),
            m('div.list-group-item.fs-5.ff-serif', mcq['opt3']),
            m('div.list-group-item.fs-5.ff-serif', mcq['opt4']),
          ]),
        ),
      ),
      m(
        'div.row.py-2.bg-light.mb-3.d-flex.align-items-center',
        {
          onclick: (e) => {
            this.state.expanded = !this.state.expanded
          },
        },
        [
          m('div.col-8.fs-5.text-dark.fw-bold', 'Discussion'),
          m(
            'div.col-4.text-end.text-dark',
            this.state.expanded
              ? m(
                  'svg',
                  {
                    class: 'bi bi-chevron-up',
                    xmlns: 'http://www.w3.org/2000/svg',
                    width: '24',
                    height: '24',
                    fill: 'currentColor',
                    viewBox: '0 0 16 16',
                  },
                  m('path', {
                    'fill-rule': 'evenodd',
                    d:
                      'M7.646 4.646a.5.5 0 0 1 .708 0l6 6a.5.5 0 0 1-.708.708L8 5.707l-5.646 5.647a.5.5 0 0 1-.708-.708l6-6z',
                  }),
                )
              : m(
                  'svg',
                  {
                    class: 'bi bi-chevron-down',
                    xmlns: 'http://www.w3.org/2000/svg',
                    width: '24',
                    height: '24',
                    fill: 'currentColor',
                    viewBox: '0 0 16 16',
                  },
                  m('path', {
                    'fill-rule': 'evenodd',
                    d:
                      'M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z',
                  }),
                ),
          ),
        ],
      ),
      this.state.expanded
        ? m(
            'div.row',
            m(
              'div.col.ff-serif.fs-5',
              { style: { 'white-space': 'pre-wrap' } },
              mcq['disc'].length ? mcq['disc'] : 'No discussion available',
            ),
          )
        : null,
    ]
  }
}

class Message {
  view(vnode) {
    return m(
      'div.row',
      m(
        'div.col.text-center',
        m(
          'div.alert.text-light ',
          { class: vnode.attrs.color },
          vnode.attrs.message,
        ),
      ),
    )
  }
}

class Loader {
  view() {
    return m('div.row.py-4', m('div.col.text-center', 'Loading...'))
  }
}

export default Home
