import m from 'mithril'

const AppState = {
  busy: false,
  mcq: {},
  index: 1,
  error: null,
  loadMCQ: async () => {
    if (AppState.busy) return
    AppState.busy = true
    m.redraw()
    console.log('sending request')
    let res = await m.request(
      'https://evokeapps.000webhostapp.com/gmp/rest.php/mcq/' + AppState.index,
    )
    if (res['error'] != null) {
      AppState.error = res['error']
    } else {
      AppState.mcq = res['payload']['mcq']
      AppState.count = res['payload']['count']
    }
    AppState.busy = false
    m.redraw()
  },
}

export default AppState
